// Generate individual weekly GA entries for MAD II and GenAI
export function getWeeklyGAs(): Deadline[] {
  const entries: Deadline[] = [];

  // MAD II weekly assessments (Weeks 1-11, objective, no proctoring)
  for (let i = 0; i < 11 && i < weeklyGADeadlines.length; i++) {
    entries.push({
      id: `mad2-wga-${i + 1}`,
      course: "MAD II",
      title: `GA ${i + 1}`,
      category: "Graded Assignment",
      dueDate: weeklyGADeadlines[i].dueDate,
    });
  }

  // GenAI weekly assessments (9 graded)
  for (let i = 0; i < 9 && i < weeklyGADeadlines.length; i++) {
    entries.push({
      id: `genai-wga-${i + 1}`,
      course: "GenAI",
      title: `GA ${i + 1}`,
      category: "Graded Assignment",
      dueDate: weeklyGADeadlines[i].dueDate,
    });
  }

  // MLP weekly assessments (10 graded)
  for (let i = 0; i < 10 && i < weeklyGADeadlines.length; i++) {
    entries.push({
      id: `mlp-wga-${i + 1}`,
      course: "MLP",
      title: `GA ${i + 1}`,
      category: "Graded Assignment",
      dueDate: weeklyGADeadlines[i].dueDate,
    });
  }

  return entries;
}

export function getAllDeadlines(): Deadline[] {
  return [...deadlines, ...getWeeklyGAs()].sort(
    (a, b) => new Date(a.dueDate).getTime() - new Date(b.dueDate).getTime()
  );
}

