import { useMemo, useState, useEffect } from "react";
import { format, differenceInDays } from "date-fns";
import { getAllDeadlines, courseGrading, type Course, type Deadline } from "@/data/deadlines";
import { CourseBadge } from "@/components/CourseBadge";
import { getDeadlineStatus, getCountdown, statusStyles } from "@/lib/deadline-utils";
import { cn } from "@/lib/utils";
import { GraduationCap, BookOpen, Clock, CalendarDays, FlaskConical, FileCode, Trophy, Laptop, PenLine, FileText, ExternalLink, Sun, Moon, Zap, Github } from "lucide-react";

const allCourses: Course[] = ["TDS", "MLP", "MAD II", "GenAI", "MAD I"];

interface SectionConfig {
  title: string;
  icon: React.ReactNode;
  filter: (d: Deadline) => boolean;
  windowDays: number; // show items due within this many days (0 = show all upcoming)
  color: string; // border accent
  link?: { title: string; url: string };
  limit?: number;
}

const sections: SectionConfig[] = [
  {
    title: "WEEKLY GA",
    icon: <FileCode className="h-4 w-4" />,
    filter: (d) => d.category === "Graded Assignment" && d.course !== "TDS",
    windowDays: 14,
    color: "border-t-primary",
  },
  {
    title: "TDS",
    icon: <FlaskConical className="h-4 w-4" />,
    filter: (d) => d.course === "TDS",
    windowDays: 90,
    color: "border-t-course-tds",
    link: { title: "TDS Portal", url: "https://tds.s-anand.net/" },
    limit: 3,
  },
  {
    title: "OPPE",
    icon: <Laptop className="h-4 w-4" />,
    filter: (d) => d.category === "Programming Exam" || d.category === "Programming Assignment",
    windowDays: 90,
    color: "border-t-course-mlp",
  },
  {
    title: "KAGGLE",
    icon: <Trophy className="h-4 w-4" />,
    filter: (d) => d.category === "Kaggle Assignment",
    windowDays: 60,
    color: "border-t-course-mlp",
  },
  {
    title: "QUIZZES",
    icon: <PenLine className="h-4 w-4" />,
    filter: (d) => d.category === "Quiz",
    windowDays: 60,
    color: "border-t-course-genai",
  },
  {
    title: "EXAMS",
    icon: <BookOpen className="h-4 w-4" />,
    filter: (d) => d.category === "Exam" && d.course !== "TDS",
    windowDays: 90,
    color: "border-t-status-overdue",
  },
];

function DeadlineRow({ title, dueDate, courses, weight, url }: { title: string; dueDate: string; courses: Course[]; weight?: string; url?: string }) {
  const status = getDeadlineStatus(dueDate);
  const countdown = getCountdown(dueDate);
  const baseStatusClass = statusStyles[status].split(" ")[0];

  return (
    <div className="group/row flex items-center justify-between gap-4 py-4 border-b border-white/[0.03] last:border-0 hover:bg-white/[0.02] transition-colors duration-300">
      <div className="flex items-center gap-4 min-w-0 flex-1">
        <div className="flex gap-1.5 shrink-0 items-center">
          {courses.map((course, i) => (
            <CourseBadge
              key={course}
              course={course}
              size="sm"
              className={cn(
                "relative transition-all duration-500 group-hover/row:scale-110",
                i === 0 ? "z-[4]" : i === 1 ? "z-[3]" : i === 2 ? "z-[2]" : "z-[1]"
              )}
            />
          ))}
        </div>
        <span className="text-[14px] font-bold text-foreground tracking-tight leading-none transition-colors whitespace-nowrap overflow-hidden text-ellipsis">
          {title}
        </span>
      </div>
      <div className="flex items-center gap-6 shrink-0">
        {url ? (
          <a
            href={url}
            target="_blank"
            rel="noopener noreferrer"
            className="flex h-6 w-8 shrink-0 items-center justify-center rounded-lg bg-primary/10 border border-primary/20 hover:bg-primary/20 hover:border-primary/40 transition-all group/link"
            title="Open Link"
          >
            <ExternalLink className="h-3 w-3 text-primary" />
          </a>
        ) : (
          <div className="h-6 w-8 shrink-0" />
        )}
        <span className="text-[10px] text-muted-foreground font-black uppercase tracking-[0.2em] hidden md:inline opacity-60 group-hover/row:opacity-100 transition-opacity w-14 text-right whitespace-nowrap">
          {format(new Date(dueDate), "MMM d")}
        </span>
        <div className={cn(
          "text-[9px] font-black px-3 py-1.5 rounded-xl w-[125px] whitespace-nowrap text-center uppercase tracking-[0.15em] transition-all duration-300",
          "ring-1 ring-inset",
          status === "overdue" || status === "due-today" ? "bg-status-overdue/10 text-status-overdue ring-status-overdue/20" :
            status === "due-soon" ? "bg-status-due-soon/10 text-status-due-soon ring-status-due-soon/20" :
              "bg-white/5 text-muted-foreground ring-white/10 opacity-60"
        )}>
          {countdown}
        </div>
      </div>
    </div>
  );
}

function SectionBox({ config, deadlines, style }: { config: SectionConfig; deadlines: Deadline[]; style?: React.CSSProperties }) {
  const now = new Date();
  const [mousePos, setMousePos] = useState({ x: 0, y: 0 });

  const handleMouseMove = (e: React.MouseEvent<HTMLDivElement>) => {
    const rect = e.currentTarget.getBoundingClientRect();
    setMousePos({ x: e.clientX - rect.left, y: e.clientY - rect.top });
  };

  const items = useMemo(() => {
    const filtered = deadlines
      .filter(config.filter)
      .filter((d) => {
        const days = differenceInDays(new Date(d.dueDate), now);
        return days >= -2 && days <= config.windowDays;
      });

    const simplified = filtered.map(d => ({
      ...d,
      displayTitle: d.title
        .replace("Online programming exam", "OPPE")
        .replace("Programming Assignments", "PA")
        .replace("Remote Online Exam", "ROE")
        .replace("End Term Exam", "ET")
        .replace("exam 1", "1")
        .replace("exam 2", "2")
        .replace("Project M", "Milestone ")
        .replace("Project ", "Milestone ")
        .replace(" (Final Submission)", "")
        .replace(" (Final)", "")
        .replace("(Wk 1-2)", "")
        .split("  ")[0]
        .split(" - ")[0]
        .trim()
    }));

    const groups: Record<string, { id: string, title: string, dueDate: string, courses: Course[], weight?: string, url?: string }> = {};
    simplified.forEach(d => {
      const key = `${d.displayTitle}-${d.dueDate}`;
      if (!groups[key]) {
        groups[key] = { id: key, title: d.displayTitle, dueDate: d.dueDate, courses: [], weight: d.weight, url: d.url };
      }
      if (!groups[key].courses.includes(d.course)) {
        groups[key].courses.push(d.course);
      }
    });

    const sorted = Object.values(groups).sort((a, b) => {
      // Prioritize OPPE exams over Programming Assignments in the OPPE section
      const aIsOPPE = a.title.toUpperCase().includes("OPPE");
      const bIsOPPE = b.title.toUpperCase().includes("OPPE");
      if (aIsOPPE && !bIsOPPE) return -1;
      if (!aIsOPPE && bIsOPPE) return 1;

      return new Date(a.dueDate).getTime() - new Date(b.dueDate).getTime();
    });
    return config.limit ? sorted.slice(0, config.limit) : sorted;
  }, [config.filter, config.title, config.windowDays, config.limit, deadlines, now]);

  if (items.length === 0) return null;

  return (
    <div
      onMouseMove={handleMouseMove}
      className={cn(
        "group glass-glow rounded-[2rem] overflow-hidden flex flex-col p-6",
        "animate-in fade-in slide-in-from-bottom-8 duration-700 ease-out"
      )}
      style={{ ...style, "--x": `${mousePos.x}px`, "--y": `${mousePos.y}px` } as any}
    >
      <div className="bento-glow" />

      <div className="relative z-10 flex items-center gap-3 mb-6">
        <div className="p-2.5 rounded-2xl bg-primary/10 text-primary">
          {config.icon}
        </div>
        <h2 className="text-[13px] font-black tracking-[0.2em] text-foreground/80">{config.title}</h2>
        <span className="ml-auto flex h-6 w-6 items-center justify-center rounded-full bg-primary/20 text-[10px] font-black text-primary">
          {items.length}
        </span>
      </div>

      <div className="relative z-10 space-y-1 mb-6">
        {items.map((item) => (
          <DeadlineRow
            key={item.id}
            title={item.title}
            dueDate={item.dueDate}
            courses={item.courses}
            weight={item.weight}
            url={item.url}
          />
        ))}
      </div>

      {config.link && (
        <div className="relative z-10 pt-4 border-t border-white/5">
          <a
            href={config.link.url}
            target="_blank"
            rel="noopener noreferrer"
            className="flex items-center gap-2 px-4 py-2 rounded-xl bg-white/5 hover:bg-white/10 border border-white/5 hover:border-primary/30 transition-all group/link w-fit"
          >
            <ExternalLink className="h-3 w-3 text-primary/60 group-hover/link:text-primary transition-colors" />
            <span className="text-[10px] font-bold text-muted-foreground group-hover/link:text-foreground transition-colors">
              {config.link.title}
            </span>
          </a>
        </div>
      )}
    </div>
  );
}

