function GradingCard() {
  return (
    <div className="glass-glow rounded-[2rem] p-8 md:col-span-full">
      <h2 className="flex items-center gap-3 text-[13px] font-black uppercase tracking-[0.2em] text-foreground/80 mb-8">
        <div className="p-2.5 rounded-2xl bg-primary/10 text-primary">
          <BookOpen className="h-4 w-4" />
        </div>
        Course Grading Formulas
      </h2>
      <div className="grid gap-6 sm:grid-cols-2 lg:grid-cols-4">
        {allCourses.filter(c => c !== "MAD I").map((course) => (
          <div key={course} className="p-5 rounded-3xl bg-secondary/20 hover:bg-secondary/40 transition-all group border border-white/[0.03]">
            <CourseBadge course={course} className="mb-4" />
            <code className="block text-[10px] text-muted-foreground font-mono leading-relaxed opacity-70 group-hover:opacity-100 transition-opacity">
              {courseGrading[course]}
            </code>
          </div>
        ))}
      </div>
    </div>
  );
}


export default function Dashboard() {
  const allDeadlines = useMemo(() => getAllDeadlines(), []);
  const [showAll, setShowAll] = useState(false);
  const [view, setView] = useState<"theory" | "projects">("theory");
  const [theme, setTheme] = useState<"light" | "dark">(() => {
    if (typeof window !== "undefined") {
      return (localStorage.getItem("theme") as "light" | "dark") || "light";
    }
    return "light";
  });

  useEffect(() => {
    if (theme === "dark") {
      document.documentElement.classList.add("dark");
    } else {
      document.documentElement.classList.remove("dark");
    }
    localStorage.setItem("theme", theme);
  }, [theme]);

  // Force scroll to top on mount and disable history restoration
  useEffect(() => {
    if ('scrollRestoration' in window.history) {
      window.history.scrollRestoration = 'manual';
    }
    window.scrollTo(0, 0);
  }, []);

  const toggleTheme = () => setTheme(prev => prev === "dark" ? "light" : "dark");

  return (
    <div className="min-h-screen pb-20">
      <header className="sticky top-0 z-50 py-4 px-6 sm:px-12 bg-background/80 backdrop-blur-md border-b border-white/5 transition-all duration-300">
        <div className="mx-auto max-w-7xl flex items-center">
          {/* Logo Section - Left */}
          <div className="flex-1 flex items-center">
            <div className="flex items-center gap-4">
              <div className="flex h-12 w-12 items-center justify-center rounded-2xl bg-primary text-white shadow-[0_0_20px_rgba(var(--primary),0.3)] shrink-0">
                <GraduationCap className="h-6 w-6" />
              </div>
              <div className="flex flex-col">
                <h1 className="text-2xl font-black text-foreground tracking-tighter leading-tight">IITM <span className="text-primary italic">Tracker</span></h1>
                <p className="text-[10px] font-bold text-muted-foreground uppercase tracking-[0.2em]">Dashboard  2026</p>
              </div>
            </div>
          </div>

          {/* Navigation Toggle - Absolute Center */}
          <div className="flex-none flex items-center justify-center">
            <div className="flex items-center gap-2 p-1 rounded-2xl bg-white/5 border border-white/10 backdrop-blur-md shadow-2xl">
              <button
                onClick={() => setView("theory")}
                className={cn(
                  "px-4 py-2 text-[10px] font-black uppercase tracking-[0.1em] rounded-xl transition-all duration-300",
                  view === "theory" ? "bg-primary text-white shadow-lg shadow-primary/20" : "text-muted-foreground hover:text-foreground"
                )}
              >
                Theory
              </button>
              <button
                onClick={() => setView("projects")}
                className={cn(
                  "px-4 py-2 text-[10px] font-black uppercase tracking-[0.1em] rounded-xl transition-all duration-300",
                  view === "projects" ? "bg-primary text-white shadow-lg shadow-primary/20" : "text-muted-foreground hover:text-foreground"
                )}
              >
                Projects
              </button>
            </div>
          </div>

          {/* Actions Section - Right */}
          <div className="flex-1 flex items-center justify-end gap-3">
            <button
              onClick={() => setShowAll(!showAll)}
              className={cn(
                "hidden md:flex items-center h-10 gap-2 rounded-xl border border-white/10 bg-white/5 backdrop-blur-md px-4 text-[10px] font-black uppercase tracking-[0.15em] transition-all duration-300",
                showAll ? "bg-primary/20 text-primary border-primary/30" : "text-muted-foreground hover:bg-white/10"
              )}
              title={showAll ? "Switch to Focus Mode" : "Switch to Timeline Mode"}
            >
              {showAll ? <Clock className="h-3.5 w-3.5" /> : <CalendarDays className="h-3.5 w-3.5" />}
              {showAll ? "Focus" : "Timeline"}
            </button>
            <div className="h-8 w-[1px] bg-white/10 mx-1 hidden md:block" />
            <button
              onClick={toggleTheme}
              className="flex h-10 w-10 items-center justify-center rounded-xl bg-white/5 border border-white/10 hover:bg-white/10 transition-all duration-300 group"
              title={theme === "dark" ? "Switch to Light Mode" : "Switch to Dark Mode"}
            >
              {theme === "dark" ? (
                <Sun className="h-4.5 w-4.5 text-yellow-500 transition-transform group-hover:rotate-45" />
              ) : (
                <Moon className="h-4.5 w-4.5 text-primary transition-transform group-hover:-rotate-12" />
              )}
            </button>
          </div>
        </div>
      </header>

      <main className="mx-auto max-w-7xl px-6 sm:px-12 py-6">
        {view === "theory" ? (
          <div className="grid gap-6 grid-cols-1 md:grid-cols-2">
            {sections.map((config, index) => (
              <SectionBox
                key={config.title}
                config={showAll ? { ...config, windowDays: 365, limit: undefined } : config}
                deadlines={allDeadlines}
                style={{ animationDelay: `${index * 100}ms` }}
              />
            ))}
            <GradingCard />
          </div>
        ) : (
          <div className="grid gap-8 grid-cols-1 md:grid-cols-2">
            {["MLP", "GenAI", "MAD I", "MAD II"].map((course, index) => (
              <ProjectBox
                key={course}
                course={course as Course}
                deadlines={allDeadlines}
                style={{ animationDelay: `${index * 150}ms` }}
              />
            ))}
          </div>
        )}
      </main>

      {/* GitHub Star Button - Bottom Left */}
      <div className="fixed bottom-6 left-6 z-[60] animate-in fade-in slide-in-from-left-8 duration-1000 delay-500 fill-mode-both">
        <a
          href="https://github.com/rahmantausif/IITM-Tracker"
          target="_blank"
          rel="noopener noreferrer"
          className="flex items-center gap-2.5 px-5 py-3 rounded-2xl bg-white/5 hover:bg-white/10 border border-white/10 hover:border-primary/30 backdrop-blur-xl transition-all duration-300 group shadow-2xl"
        >
          <div className="relative">
            <Github className="h-4.5 w-4.5 text-foreground/70 group-hover:text-primary transition-colors duration-300" />
            <div className="absolute -top-1 -right-1 h-2 w-2 bg-primary rounded-full animate-pulse shadow-[0_0_8px_rgba(var(--primary),0.6)]" />
          </div>
          <div className="flex flex-col items-start leading-none">
            <span className="text-[10px] font-black uppercase tracking-[0.15em] text-muted-foreground group-hover:text-foreground transition-colors">Star on</span>
            <span className="text-[13px] font-bold text-foreground">GitHub</span>
          </div>
        </a>
      </div>
    </div>
  );
}
