import { differenceInDays, differenceInHours, isPast, isToday } from "date-fns";

export type DeadlineStatus = "overdue" | "due-today" | "due-soon" | "upcoming" | "far";

export function getDeadlineStatus(dueDate: string): DeadlineStatus {
  const due = new Date(dueDate + "T23:59:59");
  const now = new Date();

  if (isPast(due) && !isToday(due)) return "overdue";
  if (isToday(due)) return "due-today";
  const days = differenceInDays(due, now);
  if (days <= 3) return "due-soon";
  if (days <= 7) return "upcoming";
  return "far";
}

export function getCountdown(dueDate: string): string {
  const due = new Date(dueDate + "T23:59:59");
  const now = new Date();

  if (isPast(due) && !isToday(due)) return "Past due";
  if (isToday(due)) return "Due today!";

  const days = differenceInDays(due, now);
  if (days === 0) {
    const hours = differenceInHours(due, now);
    return `${hours}h left`;
  }
  if (days === 1) return "Tomorrow";
  if (days < 7) return `${days} days left`;
  if (days < 30) return `${Math.ceil(days / 7)} weeks left`;
  return `${Math.ceil(days / 30)} months left`;
}

export const statusStyles: Record<DeadlineStatus, string> = {
  overdue: "status-overdue",
  "due-today": "status-overdue font-bold",
  "due-soon": "status-due-soon font-semibold",
  upcoming: "status-upcoming",
  far: "status-upcoming opacity-60",
};
