import { Course } from "@/data/deadlines";
import { cn } from "@/lib/utils";

const colorMap: Record<Course, string> = {
  TDS: "bg-course-tds shadow-[0_0_12px_-2px_rgba(var(--course-tds),0.4)]",
  MLP: "bg-course-mlp shadow-[0_0_12px_-2px_rgba(var(--course-mlp),0.4)]",
  "MAD II": "bg-course-mad2 shadow-[0_0_12px_-2px_rgba(var(--course-mad2),0.4)]",
  GenAI: "bg-course-genai shadow-[0_0_12px_-2px_rgba(var(--course-genai),0.4)]",
  "MAD I": "bg-course-mad1 shadow-[0_0_12px_-2px_rgba(var(--course-mad1),0.4)]",
};

const textColorMap: Record<Course, string> = {
  TDS: "text-course-tds",
  MLP: "text-course-mlp",
  "MAD II": "text-course-mad2",
  GenAI: "text-course-genai",
  "MAD I": "text-course-mad1",
};

const borderColorMap: Record<Course, string> = {
  TDS: "border-course-tds",
  MLP: "border-course-mlp",
  "MAD II": "border-course-mad2",
  GenAI: "border-course-genai",
  "MAD I": "border-course-mad1",
};

export function CourseBadge({
  course,
  className,
  size = "md"
}: {
  course: Course;
  className?: string;
  size?: "sm" | "md" | "lg"
}) {
  const sizeStyles = {
    sm: "px-2 py-0.5 text-[10px] font-bold",
    md: "px-2.5 py-0.5 text-[10px] font-extrabold",
    lg: "px-3 py-1 text-[11px] font-black uppercase tracking-widest",
  };

  return (
    <span
      className={cn(
        "inline-flex items-center rounded-full text-white ring-1 ring-white/10",
        sizeStyles[size],
        colorMap[course],
        className
      )}
    >
      {course}
    </span>
  );
}

export function CourseDot({ course, className }: { course: Course; className?: string }) {
  return <span className={cn("inline-block h-2 w-2 rounded-full", colorMap[course], className)} />;
}

export { colorMap, textColorMap, borderColorMap };
